import { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import { Text } from "../../assets/styles/component";

const Ads = styled.div`
  position: relative;
  box-sizing: border-box;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  height: 33.125rem;
  width: 32.5rem;
  padding: 2.625rem 2rem;

  @media (min-width: 2500px) {
    width: 32%;
  }

  @media (max-width: 1200px) {
    height: 33.125rem;
    width: 26.25rem;
    padding: 2rem;
  }

  text-align: left;

  border-radius: 1.563rem;
  background: ${({ bg_color }) => {
    return bg_color;
  }};
  border: 1px solid #10ace0;

  & img {
    opacity: 0;
    ${({ view }) => !view && `
      animation: 1s ease-in-out smooth_up forwards;

      @keyframes smooth_up {
        0% {
          opacity: 0;
          transform: translateY(-100px);
        }
        100% {
          opacity: 0.6;
          transform: translateY(0);
        }
      }
    `}
  }
`;

const AdsTitle = styled.h3`
  margin-bottom: 1.25rem;

  font-weight: 400;
  font-size: 2.25rem;
  line-height: 110%;
  color: #ffffff;
`;

const AdsDesc = styled(Text)`
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 150%;
  color: #ffffff;
`;

const AdsImg = styled.img`
  position: absolute;
  opacity: ${({ opacity = 1 }) => opacity};
  max-width: 70%;
  top: 0;
  right: 0;
`;

const AdsType = ({ opacity, adImg, adTitle, description, bg_color }) => {
  const [view, setView] = useState(true);
  const elem = useRef(null);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  return (
    <>
      <Ads bg_color={bg_color} ref={elem} view={view}>
        <AdsImg opacity={opacity} loading="lazy" alt="adtype" src={adImg} />
        <AdsTitle>{adTitle}</AdsTitle>
        <AdsDesc>{description}</AdsDesc>
      </Ads>
    </>
  );
};

export default AdsType;
