import styled from "styled-components";
import { Text, Title2 } from "../../assets/styles/component";

import { EngLoc } from "../../assets/constants/localizations";

import textAds from "../../assets/images/Messangers_ads.svg";
import nativeAds from "../../assets/images/Native_ads.svg";
import videoAds from "../../assets/images/Video.svg";
import AdsType from "./adstypeDesktop";

const StyledAbout = styled.section`
  padding: 9.375rem 0 9.375rem 0;
  margin: 0 8.3%;
  gap: 2.063rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 100rem;

  @media (min-width: 1950px) {
    margin: 0 auto;
  }

  @media (max-width: 1290px) {
    margin: 0 3.3%;
  }
`;

const AboutPart = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.875rem;

  @media (max-width: 1700px) {
    flex-wrap: wrap;
    justify-content: center;
    gap: 1.875rem;

    width: 100%;
  }
`;

const HightTitle = styled(Title2)`
  font-weight: 400;
  font-size: 6rem;
  line-height: 110%;
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
`;

const AboutText = styled(Text)`
  width: 100%;
  max-width: 62.5rem;
  height: fit-content;
  margin-bottom: 4.813rem;

  color: #3c3c3c;
  font-weight: 300;
  font-size: clamp(1rem, 4vw, 1.25rem);
  line-height: 150%;
  text-align: left;
`;

const About = ({ language }) => {
  const AdsList = [
    {
      title:
        language === "Ru"
          ? "Реклама в мессенджерах"
          : EngLoc.about.adsType.messangers.title,
      desc:
        language === "Ru"
          ? "Автоматизация процессов от выбора паблишеров, до размещений и пост-анализа. Реклама в каналах, чат-ботах и Telegram ads."
          : EngLoc.about.adsType.messangers.desc,
      img: textAds,
      bg_color:
        "radial-gradient(76.6% 76.6% at 97.21% 23.4%, #36DBFF 0%, rgba(54, 219, 255, 0) 100%), #10ACE0",
    },
    {
      title:
        language === "Ru"
          ? "Нативная реклама в социальных сетях"
          : EngLoc.about.adsType.native.title,
      desc:
        language === "Ru"
          ? "Размещение во всех популярных соцсетях и блогах. Автоматизация, статистка, упрощение взаиодействия с паблишерами. Оплата по моделям CPM, CPV, CPA."
          : EngLoc.about.adsType.native.desc,
      img: nativeAds,
      bg_color:
        "radial-gradient(76.6% 76.6% at 97.21% 23.4%, #FF4E7B 0%, rgba(244, 65, 110, 0) 100%), #F72EBD",
      opacity: 0.2,
    },
    {
      title:
        language === "Ru"
          ? "Видео-реклама на сайтах"
          : EngLoc.about.adsType.video.title,
      desc:
        language === "Ru"
          ? "In-stream и mobile fullscrean видео на тысячах сайтов, с возможностью таргетинга и контроля эффективности."
          : EngLoc.about.adsType.video.desc,
      img: videoAds,
      opacity: 0.4,
      bg_color:
        "radial-gradient(76.6% 76.6% at 97.21% 23.4%, rgba(213, 53, 109, 0.71) 0%, rgba(177, 62, 103, 0) 100%), #2B2257",
    },
  ];

  return (
    <StyledAbout id={"about"}>
      <HightTitle>SocialJet</HightTitle>
      <AboutText>
        {language === "Ru"
          ? `С 2015 года являемся одними из крупнейших поставщиков трафика в
            социальных сетях и мессенджерах. Предоставляем сервис, аналитику и
            поддержку`
          : EngLoc.about.orbit}
      </AboutText>
      <AboutPart>
        {AdsList.map((ad, index) => (
          <AdsType
            key={index}
            adTitle={ad.title}
            description={ad.desc}
            adImg={ad.img}
            line={ad.line}
            bg_color={ad.bg_color}
            opacity={ad.opacity && ad.opacity}
          />
        ))}
      </AboutPart>
    </StyledAbout>
  );
};

export default About;
